import React from 'react';
import { useState } from 'react';
//import HomeImage from '../Images/images.png';
import './Home.css';
import { IconButton, InputAdornment } from '@mui/material';
import RemoveRedEyeIcon from '@mui/icons-material/RemoveRedEye';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';

function Home({ navigation }) {
    const [formData, setFormData] = useState({
        email:'',
        password:''
    })

    const [errors, setErrors] = useState({})

    const handleChange = (event) => {
        const {name, value} = event.target;
        setFormData({
            ...formData, [name]: value
        })
    }

    const handleSubmit = (event) => {
        event.preventDefault();
        const validationErrors = {}
        if(!formData.email.trim()){
            validationErrors.email = "email is required!"
        } else if(!/\S+@\S+\.\S+/.test(formData.email)){
            validationErrors.email = "email is not valid"
        }

        if(!formData.password.trim()){
            validationErrors.password = "password is required!"
        } else if(formData.password.length < 6){
            validationErrors.password = "password should be at least 6 characters."
        }

        setErrors(validationErrors)

        if(Object.keys(validationErrors).length === 0){
            navigation.navigate('User Details')
        }
    }

    const [visible, setVisible] = useState(false)

    const EndAdornment = ({visible, setVisible}) => {
        return <InputAdornment position="end">
            <IconButton onClick={() => setVisible(!visible)}>
                {visible ? <VisibilityOffIcon /> : <RemoveRedEyeIcon />}
            </IconButton>
        </InputAdornment> 
    }   
    /*const HomeImage = () => (
        <>
            <a href="https://pngtree.com/freepng/team-meeting-in-conference-room_5416761.html">
                <img alt="HomeImage" src={HomeImage}></img></a>
        </>
    )*/

    return (
        <>
            <h1 style={{ textAlign: 'start' }}>User Account</h1>
            <form onSubmit = {handleSubmit}>
                <div className="frame">
                    <div className="innerFrame">
                        <h2>Sign In</h2>
                        <label className='email'>Email</label>
                        <input
                            type="text"
                            name="email"
                            onChange={handleChange}
                            placeholder='email@example.com'
                            autoComplete='off' />
                        <br />
                        {errors.email && <span>{errors.email}</span>}
                        
                        <label className='password'>Password</label>
                        <input
                            type={visible ? "password" : "text"}
                            onChange={handleChange} 
                            placeholder='******'
                            InputProps={{
                                endAdornment: <EndAdornment visible={visible} setVisible={setVisible}/>,
                            }}/>
                        <br />
                            {errors.password && <span>{errors.password}</span>}
                        <br/>
                        <br/>
                        <button 
                        className="styled" 
                        type="submit">Submit</button>
                    </div>                   
                </div>
            </form>
        </>
    )
}

export default Home;