import React from 'react';
import { useState } from 'react';
import './UserDetails.css';
//import Home from './Home';


function UserDetails({ navigation }) {
  const [details, setDetils] = useState({
    fName: '',
    lName: '',
    gender: '',
    postCode: ''
  })

  const [error, setError] = useState({})

  const handleChange = (Event) => {
    const { user, values } = Event.target;
    setDetils({
      ...details, [user]: values
    })
  }

  const handleSubmit = (Event) => {
    Event.preventDefault();
    const validationError = {}
    if (!details.fName.trim()) {
      validationError.fName = "first name is required!"
    } 

    if (!details.lName.trim()) {
      validationError.lName = "last name is required!"
    }

    if (!details.postCode.trim()) {
      validationError.postCode = "post code is required!"
    } else if(details.postCode.length > 999 && details.postCode.length < 10000){
      validationError.postCode = "postcode should be at least four digit."
    }

    setError(validationError)

    if(Object.keys(validationError).length === 0){
      navigation.navigate('./Home')
    }
  }

  return (
    <>
    <h1> </h1>
      <form onSubmit={handleSubmit}>
        <div className="frame-details">
          <div className="inner-frame">
            <label>First Name: </label>
            <input
              type="text"
              name="fName"
              onChange={handleChange}
              placeholder='First Name'
              autoComplete='off' />
            <br />
            {error.fName && <span>{error.fName}</span>}

            <label>Last Name: </label>
            <input
              type="text"
              name="lName"
              onChange={handleChange}
              placeholder='Last Name'
              autoComplete='off' />
            <br />
            {error.lName && <span>{error.lName}</span>}

            <label>Gender: </label>
            <select name='gender' onChange={handleChange}>
              <option value={""}>Please choose your Gender</option>
              <option value="Female">Female</option>
              <option value="Male">Male</option>
              <option value="Other">Other</option>
            </select><br />
            

            <label>Post Code: </label>
            <input
              type="int"
              name="postCode"
              onChange={handleChange}
              placeholder='0000'
              autoComplete='off' />
            <br />
            {error.postCode && <span>{error.postCode}</span>}
            <br />
            <button
              className="submitButton"
              type="submit">Submit</button>

          </div>
        </div>
      </form>
    </>
  )
}

export default UserDetails;